// MenuControls.jsx
import React from "react";
import { Grid, Column, Dropdown, Button } from "@carbon/react";
import { ViewFilled, Filter } from "@carbon/icons-react";
import "../../styles/components/_hoshin-kanri.scss";

// Structural presentation options (View logic)
const viewItems = [
  { id: "matrix", text: "Matrix viewFull objective chain" },
  { id: "lto-cards", text: "Card viewLong-term objectives" },
  { id: "pa-cards", text: "Card viewPriorities and activities" },
  { id: "ao-cards", text: "Card viewYr 1 annual objectives" },
  { id: "kpi-cards", text: "Card viewKPIs" },
];

// Semantic filters (Filter logic)
const filterItems = [
  { id: "header-lto", text: " Long-term objectives ", disabled: true },
  { id: "lto-north-star", text: "LTONorth Star" },
  { id: "lto-recast-archive", text: "LTOrecast archive" },
  { id: "lto-ai-infrastructure", text: "LTOAI infrastructure" },
  { id: "lto-method-innovation", text: "LTOmethod innovation" },
  { id: "lto-recode-canon", text: "LTOrecode the canon" },

  { id: "header-pa", text: " Priorities and Activities ", disabled: true },
  { id: "pa-digitise-archive", text: "PAdigitise archive" },
  { id: "pa-critical-inquiry", text: "PAcritical inquiry" },
  { id: "pa-interactive-platform", text: "PAinteractive platform" },
  { id: "pa-expert-interviews", text: "PAexpert interviews" },

  { id: "header-ao", text: " Annual Objectives ", disabled: true },
  { id: "ao-equity-audit", text: "AOequity audit" },
  { id: "ao-literature-review", text: "AOliterature review" },
  { id: "ao-ethics-access", text: "AOethics + access" },
  { id: "ao-analysis-pipeline", text: "AOanalysis pipeline" },
  { id: "ao-stakeholder-interviews", text: "AOstakeholder interviews" },
  { id: "ao-prototype-taxonomy", text: "AOprototype taxonomy" },

  { id: "header-kpi", text: " KPIs ", disabled: true },
  { id: "kpi-publications", text: "KPIpublication outputs" },
  { id: "kpi-taxonomy-usage", text: "KPItaxonomy usability" },
  { id: "kpi-inclusive-metadata", text: "KPIinclusive metadata" },
  { id: "kpi-interviews", text: "KPIstakeholder interviews" },
  { id: "kpi-digitisation", text: "KPIdigitisation progress" },
];

const MenuControls = ({ selectedView, setView, selectedFilter, setFilter }) => {
  const handleReset = () => {
    setView(null);
    setFilter(null);
  };

  return (
    <div className="menu-controls">
      <Grid condensed fullWidth className="filter-dropdown-row">
        <Column lg={5} md={4} sm={2}>
          <Dropdown
            id="view-dropdown"
            label={
              <span style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                <ViewFilled size={16} /> Choose a view
              </span>
            }
            items={viewItems}
            selectedItem={selectedView}
            onChange={({ selectedItem }) => setView(selectedItem)}
            itemToString={(item) => (item ? item.text : "")}
            style={{ maxWidth: "20rem" }}
          />
        </Column>
        <Column lg={5} md={4} sm={2}>
          <Dropdown
            id="filter-dropdown"
            label={
              selectedFilter ? selectedFilter.text : (
                <span style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                  <Filter size={16} /> Apply a filter
                </span>
              )
            }
            items={filterItems}
            selectedItem={selectedFilter}
            onChange={({ selectedItem }) => setFilter(selectedItem)}
            itemToElement={(item) =>
              item.disabled ? (
                <div className="dropdown-header">{item.text}</div>
              ) : (
                <span>{item.text}</span>
              )
            }
            itemToString={(item) => (item ? item.text : "")}
            style={{ maxWidth: "20rem" }}
          />
        </Column>
        <Column lg={5} md={4} sm={2} className="filter-reset">
          <div className="reset-button-wrapper">
            <Button kind="tertiary" size="sm" onClick={handleReset}>
              Reset
            </Button>
          </div>
        </Column>
      </Grid>
    </div>
  );
};

export default MenuControls;